import React from 'react';
import {Field, reduxForm} from "redux-form";
import {
    complexity,
    copy,
    email,
    maxLengthCreator,
    minLengthCreator,
    pass,
    required
} from "../../utils/validator/validator";
import {Input} from "../../conponent/FormsControll/FormsControll";
import "./../../conponent/FormsControll/style.css"
import {NavLink} from "react-router-dom";

const maxLength16 = maxLengthCreator(16)
const minLength6 = minLengthCreator(6)

let Registration = (props) => {
    const {handleSubmit} = props;
    return <div className={'reg_Section'}>
        <h1 className={'text-regi'}>REGISTRATION</h1>

        <form onSubmit={handleSubmit}>
            <div>
                <div>Nickname</div>
                <Field component={Input}
                       placeholder={'You`r Name'}
                       name={'name'}
                       validate={[required, minLength6, maxLength16]}/>
            </div>

            <div>
                <div>Login</div>
                <Field required
                       name={'login'}
                       component={Input}
                       placeholder={'Login'}
                       validate={[email,required, minLength6]}/>
            </div>
            <div>
                <div>Password</div>
                <Field required
                       placeholder={'Password'}
                       component={Input}
                       name={'repeat-password'}
                       type="password"
                       validate={[ complexity,pass,required, minLength6, maxLength16]}/>

                <div>Repeat the password</div>
                <Field required
                       placeholder={'Password'}
                       component={Input}
                       type="password"
                       name={'password'}
                       validate={[copy,required, minLength6, maxLength16]}/>
            </div>

            {props.error && <div className={'errorEmaOrPass'}>{props.error} Это хуйня, давай заново </div>}
            <div className={'but-prot'}>
                <div className={'but-prot-item'}> <button value="Enter" id={'ent-but'}  >Enter</button> </div>
                <div onClick={props.haveKey} className={'but-prot-item'}>You have a key </div>
                <div className={'but-prot-item'}> <NavLink to='/login' id={'a'}>Go back to Login</NavLink></div>
            </div>

        </form>
    </div>
};
const ReduxRegistrationForm = reduxForm({form: 'registration'})(Registration);

export default ReduxRegistrationForm;