import React, {PureComponent} from 'react';

import ReduxRegistrationForm from "./Registration";
import './style/style.css';
import {compose} from "redux";
import {connect} from "react-redux";
import {HaveKeyDispatch, RegistrationThunk} from "./RegistrationReducer";
import {RegistrationMessage, RegistrationStatus} from "../../user-selector/RegistrationSelector";
import {redirectInLogin} from "../../hoc/RegistrationOnLogin";
import Protected from "./Protected";
import {Redirect, Route} from "react-router";


let RegistrationCont =(props)=> {


    let EntryForm = (formData) => {
        debugger
        formData.name=formData.name.match(/(\w+)/gim);
        console.log(formData);
        props.RegistrationThunk(formData)
        return <Redirect to={`/protected`}/>;
    };
    let haveKey=()=>{
        props.HaveKeyDispatch()
    }

    return <div>

            {props.statusREG && <Protected  haveKey={haveKey} />}
           {!props.statusREG && <ReduxRegistrationForm
               onSubmit={EntryForm}
               userEntry={props.userEntry}
               haveKey={haveKey}
            />}
        </div>
}

const mapStateTo = (state) => ({
    message: RegistrationMessage(state)
    , statusREG: RegistrationStatus(state)
});
export default compose(connect(mapStateTo, {RegistrationThunk,HaveKeyDispatch}))(RegistrationCont)

