import React, {useState} from 'react';

import './style/style.css';
import {compose} from "redux";
import {connect} from "react-redux";
import {RegistrationThunk, SendKeyAuthorization} from "./RegistrationReducer";
import {RegistrationStatusAuthorization} from "../../user-selector/RegistrationSelector";
import {redirectInLogin} from "../../hoc/RegistrationOnLogin";
import {Redirect} from "react-router";
import {NavLink} from "react-router-dom";


const Protected = props => {
    debugger
    let [key, setKey]=useState("");
    let [numb, setNumb]=useState(4);


    let set=(value)=>{
        setKey(value.target.value)
    };
    let send=()=>{
        setKey('')
        props.SendKeyAuthorization(key)
    };
    let d=()=>{
        window.location = '/login'
    };

    return <div className={'prot'}>
        <h1 >KEY</h1>
        <div className={'prot-text'}>Field for the key code</div>
            <input value={key} onChange={set} className={'inpu-prot'} />


        {props.statusA && <div>Redirect to login in 4 seconds</div>}
        <div className={'dis'}>{props.statusA && setTimeout(()=>d(),4000)}</div>

            <div className={'but-prot'}>
                <div className={'but-prot-item'} onClick={()=>send()}>Enter</div>
                <div className={'but-prot-item'} onClick={()=>props.haveKey()}>Come Back Authorization</div>
                <div className={'but-prot-item'} ><NavLink to='/login' onClick={()=>props.haveKey()} id={'a'}>Go back to your Login</NavLink></div>
            </div>

    </div>
};

const mapStateTo = (state) => ({
    statusA: RegistrationStatusAuthorization(state)
});
export default compose( connect(mapStateTo, {RegistrationThunk,SendKeyAuthorization}))(Protected)

